import { combineReducers, Reducer } from 'redux';
import loadingReducer, { TLoading } from '../loadingReducer';
import quoteTokenReducer, { TQuoteToken } from '../quoteTokenReducer';
import tokenListReducer from '../tokenReducer';
import swapSettingReducer, { TSwapSetting } from '../swapSettingReducer';
import { ITokenDetail } from '../../model/tokenModel';

export type IReducer = {
  loading: TLoading;
  quoteToken: TQuoteToken;
  tokens: ITokenDetail[];
  swapSetting: TSwapSetting;
};

export const rootReducer = combineReducers({
  loading: loadingReducer,
  quoteToken: quoteTokenReducer,
  tokens: tokenListReducer,
  swapSetting: swapSettingReducer,
}) as Reducer<IReducer>;
