import { reactLocalStorage } from 'reactjs-localstorage';
import { handleActions } from 'redux-actions';
import { BEP20_PROXY } from '../config/web3Config';
import {
  TXN_TIMEOUT,
  TXN_GAS,
  TXN_PRIORITY_LOW,
  TXN_PRIORITY_MEDIUM,
  TXN_PRIORITY_HIGHT,
} from '../config/appSwapConfig';
import cryptoHelper from '../helper/cryptoHelper';
import {
  updateSwapSetting,
  resetSwapSetting,
  setPassphrase,
  clearPassphrase,
  setConnectStatus,
} from './swapSettingActionCreater';

export type TTxnPriority = {
  gasPrice: number;
  slip: number;
};

export type TSwapSetting = {
  isConnected: boolean;
  passphrase: string;
  mnemonic: string;
  adress: string;
  proxy: string;
  tnxTimeout: number;
  tnxGas: number;
  txnPriorityLow: TTxnPriority;
  txnPriorityMedium: TTxnPriority;
  txnPriorityHight: TTxnPriority;
  defaultTokenInput: string;
};

export const swapSettingDefault: TSwapSetting = {
  isConnected: false,
  passphrase: '',
  mnemonic: '',
  adress: '',
  proxy: BEP20_PROXY,
  tnxTimeout: TXN_TIMEOUT,
  tnxGas: TXN_GAS,
  txnPriorityLow: TXN_PRIORITY_LOW,
  txnPriorityMedium: TXN_PRIORITY_MEDIUM,
  txnPriorityHight: TXN_PRIORITY_HIGHT,
  defaultTokenInput: '',
};

const SWAP_SETTING = 'SWAP_SETTING';
const initialState: TSwapSetting = reactLocalStorage.getObject(
  SWAP_SETTING,
  swapSettingDefault,
  true
) as TSwapSetting;

export default handleActions(
  {
    [updateSwapSetting as any]: (state: TSwapSetting, action): TSwapSetting => {
      const {
        mnemonic,
        adress,
        proxy,
        tnxTimeout,
        tnxGas,
        txnPriorityLow,
        txnPriorityMedium,
        txnPriorityHight,
        defaultTokenInput,
      } = action.payload;
      const newSetting: TSwapSetting = {
        ...state,
        mnemonic:
          state.mnemonic === mnemonic.trim()
            ? state.mnemonic
            : cryptoHelper.encodeAES(mnemonic.trim()),
        adress: adress.trim().toLowerCase(),
        proxy: proxy.trim().toLowerCase(),
        tnxTimeout,
        tnxGas,
        txnPriorityLow,
        txnPriorityMedium,
        txnPriorityHight,
        defaultTokenInput: defaultTokenInput.trim().toLowerCase(),
      };

      reactLocalStorage.setObject(SWAP_SETTING, newSetting);

      return newSetting;
    },

    [resetSwapSetting as any]: (state: TSwapSetting): TSwapSetting => {
      reactLocalStorage.setObject(SWAP_SETTING, swapSettingDefault);

      return swapSettingDefault;
    },

    [setPassphrase as any]: (state: TSwapSetting, action): TSwapSetting => {
      const newSetting: TSwapSetting = {
        ...state,
        passphrase: !!action.payload.passphrase.trim()
          ? cryptoHelper.encodeAES(action.payload.passphrase.trim())
          : action.payload.passphrase.trim(),
      };

      reactLocalStorage.setObject(SWAP_SETTING, newSetting);

      return newSetting;
    },

    [clearPassphrase as any]: (state: TSwapSetting): TSwapSetting => {
      const newSetting: TSwapSetting = {
        ...state,
        passphrase: '',
      };

      reactLocalStorage.setObject(SWAP_SETTING, newSetting);

      return newSetting;
    },

    [setConnectStatus as any]: (state: TSwapSetting, action): TSwapSetting => {
      const newSetting: TSwapSetting = {
        ...state,
        isConnected: action.payload.isConnected,
      };

      reactLocalStorage.setObject(SWAP_SETTING, newSetting);

      return newSetting;
    },
  },
  initialState
);
