import { utils, Wallet } from 'ethers';
import { AES, enc } from 'crypto-js';
import { SECRET_KEY } from '../config/secretConfig';

function encodeAES(v: string): string {
  return AES.encrypt(v, SECRET_KEY).toString();
}

function decodeAES(v: string): string {
  return AES.decrypt(v, SECRET_KEY).toString(enc.Utf8);
}

function getWallet(mnemonicCrypto: string, passphraseCrypto?: string): Wallet {
  let mnemonic: string = decodeAES(mnemonicCrypto);
  let passphrase: string;
  let hdNode: utils.HDNode;
  let wallet: Wallet;

  if (!!passphraseCrypto) {
    passphrase = decodeAES(passphraseCrypto);
    hdNode = utils.HDNode.fromMnemonic(mnemonic, passphrase).derivePath(
      utils.defaultPath
    );
    wallet = new Wallet(hdNode.privateKey);
  } else {
    wallet = Wallet.fromMnemonic(mnemonic);
  }

  return wallet;
}

const helper = {
  encodeAES,
  decodeAES,
  getWallet,
};

export default helper;
