import { createActions } from 'redux-actions';

export const {
  updateSwapSetting,
  resetSwapSetting,
  setPassphrase,
  clearPassphrase,
  setConnectStatus,
} = createActions(
  'updateSwapSetting',
  'resetSwapSetting',
  'setPassphrase',
  'clearPassphrase',
  'setConnectStatus',
  {
    prefix: 'SWAP',
  }
);

const action = {
  updateSwapSetting,
  resetSwapSetting,
  setPassphrase,
  clearPassphrase,
  setConnectStatus,
};

export default action;
